.user {
  position: relative;
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: flex;

  @media (min-width: $tablet-width) {
    display: none;
  }
}

.user__link {
  position: relative;
  font-weight: bold;
  color: $dark-blue-grey;

  &--tel {
    @include round (40px, 40px, 50%, $mango);
    padding: 11px 11px;
  }

  &--email {
    @include round (40px, 40px, 50%, $lighter-mango);
    padding: 11px 11px;
  }

  &--login {
    display: block;
    padding: 6px 39px;
    font-size: 17px;
    font-weight: bold;
    line-height: 30px;
    text-align: center;
    color: $dark-grey;
    background-color: $light-gold;
    border-radius: 25px;
  }

  &--tel:hover,
  &--tel:focus {
    background-color: $dusty-orange;
  }

  &--email:hover,
  &--email:focus {
    background-color: $mango;
  }

  &--login:hover,
  &--login:focus {
    background-color: $lighter-mango;
  }

  &--tel:active {
    background-color: $dusty-orange;
  }

  &--email:active {
    background-color: $mango;
  }

  &--tel:active .user__icon {
    opacity: 0.3;
  }

  &--email:active .user__icon {
    opacity: 0.3;
  }

  &--login:active {
    color: $dark-grey-opacity30;
    background-color: $lighter-mango;
  }
}
