.show {
  margin-top: 17px;
  margin-bottom: 61px;

  @media (min-width: $tablet-width) {
    margin-top: 44px;
    margin-bottom: 130px;
  }

  @media (min-width: $desktop-width) {
    @include vertical-paddings (63px, 117px);
    width: 950px;
    margin: 0;
    padding-left: 0;
    padding-right: 0;
  }
}

.show__button {
  position: relative;
  margin: 0 auto;
  padding-left: 33px;
  font-size: 17px;
  font-weight: bold;
  line-height: 30px;
  color: $dark-slate-blue;
  background-color: transparent;
  border: none;

  &::before,
  &::after {
    content: "";
    position: absolute;
    width: 18px;
    height: 2px;
    top: 15px;
    left: 0;
    background-color: $dark-slate-blue;
  }

  &::after {
    transform: rotate(90deg);
  }

  &:hover,
  &:focus {
    color: $dark-slate-blue-opacity60;
  }

  &:hover::before,
  &:hover::after,
  &:focus::before,
  &:focus::after {
    background-color: $dark-slate-blue-opacity60;
  }

  &:active {
    color: $dark-slate-blue-opacity30;
  }

  &:active::before,
  &:active::after {
    background-color: $dark-slate-blue-opacity30;
  }

  @media (min-width: $tablet-width) {
    padding-left: 52px;
    font-size: 20px;

    &::before,
    &::after {
      width: 27px;
      height: 3px;
    }
  }

  @media (min-width: $desktop-width) {
    margin-left: 344px;
    padding-left: 49px;
  }
}

.show__list {
  list-style-type: none;
  margin: 0;
  margin-top: 20px;
  padding: 0;
  display: flex;

  @media (min-width: $tablet-width) {
    @include vertical-paddings (25px, 25px);
    padding-left: 28px;
    padding-right: 28px;
    margin-top: 48px;
    width: 688px;
    background-color: $white;
    border-radius: 20px;
  }

  @media (min-width: $desktop-width) {
    width: 950px;
    margin-top: 67px;
  }
}

.show__link {
  position: relative;
  display: block;
  color: $dark-grey;
  background-color: $white;

  &:not(.show__link--active):not(.show__link--switch):hover,
  &:not(.show__link--active):not(.show__link--switch):focus {
    color: $dark-grey-opacity60;
  }

  &:not(.show__link--active):not(.show__link--switch):active {
    @include round (50px, 50px, 50%, $light-gold);
    color: $dark-grey;
  }

  &--switch {
    width: 133px;
    height: 50px;
  }

  &--next {
    @include border-left-radius (20px, 20px);
  }

  &--previous {
    @include border-right-radius (20px, 20px);
  }

  &--next::before {
    content: "";
    position: absolute;
    top: 14px;
    left: 63px;
    border: 10px solid #1d2e5b;
    border-left: none;
    border-top: 10px solid transparent;
    border-bottom: 10px solid transparent;
  }

  &--previous::before {
    content: "";
    position: absolute;
    top: 14px;
    left: 59px;
    border: 10px solid #1d2e5b;
    border-right: none;
    border-top: 10px solid transparent;
    border-bottom: 10px solid transparent;
  }

  &--next:hover::before,
  &--next:focus::before,
  &--previous:hover::before,
  &--previous:focus::before {
    opacity: 0.6;
  }

  &--next:active::before,
  &--previous:active::before {
    opacity: 0.3;
  }

  @media (min-width: $tablet-width) {
    width: 50px;
    height: 50px;
    padding: 10px 20px;
    font-size: 20px;
    font-weight: bold;
    line-height: 30px;
    color: $dark-grey;
    background-color: transparent;

    &--active {
      @include round (50px, 50px, 50%, $light-gold);
    }

    &--switch {
      width: 30px;
      height: 50px;
      padding: 0;
    }

    &--next::before {
      left: 25px;
    }

    &--previous::before {
      left: 13px;
    }
  }

  @media (min-width: $desktop-width) {
    &--next::before {
      left: 16px;
    }

    &--previous::before {
      left: 4px;
    }
  }
}

.show__item {
  position: relative;

  &--page {
    display: none;
  }

  &--next {
    margin-right: 6px;
  }

  @media (min-width: $tablet-width) {
    &--page {
      display: block;
    }

    &--next {
      margin-right: 25px;
      margin-left: auto;
    }
  }
}
