.country-list {
  list-style-type: none;
  padding: 0;

  @media (min-width: $tablet-width) {
    padding-left: 35px;

    &::before {
      content: "";
      position: absolute;
      top: 469px;
      left: 40px;
      width: 141px;
      height: 141px;
      border-radius: 20px;
      background-color: $pale-grey-blue;
    }
  }

  @media (min-width: $desktop-width) {
    padding: 0;

    &::before {
      margin: 0;
      padding: 0;
      top: -72px;
      left: -72px;
    }

    &::after {
      content: "";
      position: absolute;
      width: 681px;
      height: 81px;
      bottom: 0;
      left: -20px;
      background-image: linear-gradient(to top, $pale-grey, transparent);
    }
  }
}

.country-list__item {
  position: relative;
  width: 270px;

  @media (min-width: $tablet-width) {
    display: flex;
    width: 616px;
  }
}

.country-list__item:not(:first-child) {
  margin-top: 10px;
}

.country-list__container {
  position: relative;
  z-index: 2;
  display: flex;
  justify-content: space-between;
  width: 270px;
  margin-top: -11px;
  padding: 24px;
  border-radius: 10px;
  background-color: $white;

  @media (min-width: $tablet-width) {
    @include border-left-radius (20px, 20px);
    @include border-right-radius (0, 0);
    position: relative;
    z-index: 0;
    order: 1;
    display: block;
    margin-top: 0;
    width: 435px;
    padding: 30px;
    padding-bottom: 10px;
  }

  @media (min-width: $desktop-width) {
    width: 372px;
    padding: 21px 30px;
  }
}

.country-list__image {
  position: relative;
  z-index: 1;
  width: 270px;
  display: block;

  @media (min-width: $tablet-width) {
    position: relative;
    z-index: 0;
    order: 2;
    width: 186px;
    height: 140px;
  }
}

.country-list__image img {
  @include border-top-radius (10px, 10px);
  display: block;

  @media (min-width: $tablet-width) {
    @include border-right-radius (20px, 20px);
    border-top-left-radius: 0;
  }
}

.country-list__wrapper {
  @media (min-width: $tablet-width) {
    margin-bottom: 15px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 21px;
  }
}

.country-list__name {
  margin: 0;
  margin-bottom: 4px;
  font-size: 21px;
  font-weight: bold;
  line-height: 21px;
  color: $dark-grey;

  @media (min-width: $tablet-width) {
    margin-bottom: 5px;
    font-size: 24px;
    line-height: 24px;
  }
}

.country-list__slogan {
  margin: 0;
  font-weight: 500;
  line-height: 18px;
  color: $grey-opacity;

  @media (min-width: $tablet-width) {
    font-size: 20px;
  }
}

.country-list__travelers {
  position: relative;
  margin: 0;
  padding-left: 18px;
  font-weight: 500;
  line-height: 30px;
  text-align: right;
  color: $grey-opacity;

  &::before {
    content: "";
    position: absolute;
    top: 8px;
    left: 0;
    width: 11px;
    height: 11px;
    background-image: url("../img/user.svg.png");
    background-repeat: no-repeat;
    background-size: 11px 11px;
  }

  @media (min-width: $tablet-width) {
    position: relative;
    padding-left: 30px;
    font-size: 20px;
    text-align: left;

    &::before {
      content: "";
      top: 6px;
      width: 21px;
      height: 20px;
      background-size: 21px 20px;
    }
  }
}

.country-list__flag {
  position: absolute;
  top: 87px;
  left: 25px;
  z-index: 3;

  @media (min-width: $tablet-width) {
    top: 16px;
    left: 398px;
  }

  @media (min-width: $desktop-width) {
    top: 22px;
    left: 337px;
  }
}
